	module.exports = "/* RemoteStorage widget styles */\n\n.rs-widget {\n  box-sizing: border-box;\n  z-index: 21000000; /* Make sure we're on a reasonably high visibility layer */\n  overflow: hidden;\n  max-width: 350px;\n  padding: 10px;\n  margin: 10px;\n  border-radius: 3px;\n  background-color: #fff;\n  box-shadow: 0 1px 2px 0 rgba(0,0,0,0.1), 0 3px 8px 0 rgba(0,0,0,0.2);\n  font-family: arial, sans-serif;\n  font-size: 16px;\n  color: #333;\n  will-change: max-height, height, width, opacity, max-width, background, box-shadow;\n  transition-property: width, height, opacity, max-width, max-height, background, box-shadow;\n  transition-duration: 300ms;\n}\n\n.rs-widget * {\n  box-sizing: border-box;\n}\n\n.rs-widget .rs-hidden {\n  display: none;\n}\n\n.rs-box {\n  overflow: hidden;\n  will-change: height;\n  transition-property: height, width, max-height;\n  transition-duration: 300ms;\n  transition-timing-function: ease-in;\n  opacity: 0;\n  max-height: 0px;\n}\n\n.rs-box.rs-selected {\n  opacity: 1;\n  max-height: 420px;\n}\n\n/* Main logo */\n.rs-main-logo {\n  float: left;\n  height: 36px;\n  width: 36px;\n  margin-top: 1px;\n  margin-right: 0.625em;\n  transition: margin-left 300ms ease-out, transform 300ms ease-out;\n  cursor: pointer;\n}\n.rs-widget .rs-backend-remotestorage svg#rs-main-logo-remotestorage,\n{\n  display: normal;\n}\n.rs-widget[class*=\"rs-backend-\"]:not(.rs-backend-remotestorage) svg#rs-main-logo-remotestorage {\n  display: none;\n}\n.rs-widget.rs-backend-dropbox svg#rs-main-logo-dropbox {\n  display: normal;\n}\n.rs-widget:not(.rs-backend-dropbox) svg#rs-main-logo-dropbox {\n  display: none;\n}\n.rs-widget.rs-backend-googledrive svg#rs-main-logo-googledrive {\n  display: normal;\n}\n.rs-widget:not(.rs-backend-googledrive) svg#rs-main-logo-googledrive {\n  display: none;\n}\n\npolygon.rs-logo-shape {\n  fill: #FF4B03;\n}\n\npolygon.rs-logo-shape,\n#rs-main-logo-dropbox path,\n#rs-main-logo-googledrive path {\n  transition-property: fill;\n  transition-duration: 0.5s;\n}\n\n.rs-offline polygon.rs-logo-shape,\n.rs-offline #rs-main-logo-dropbox path,\n.rs-offline #rs-main-logo-googledrive path {\n  fill: #888;\n  transition-property: fill;\n  transition-duration: 0.5s;\n}\n\n/* Hide everything except logo when connected and clicked outside of box */\n.rs-closed {\n  max-width: 56px;\n  background-color: transparent;\n  box-shadow: none;\n  opacity: 0.5;\n\n  transition: max-height 100ms ease-out 0ms, max-width 300ms ease-out 300ms, background 300ms ease-in 200ms, opacity 300ms ease 200ms; \n}\n\n.rs-closed:hover {\n  cursor: pointer;\n  opacity: 1;\n}\n\n.rs-box-initial {\n  transition-duration: 0ms;\n}\n\n.rs-box-initial:hover {\n  cursor: pointer;\n}\n\n.rs-widget a {\n  color: #0093cc;\n}\n\n/* HEADLINE */\n.rs-small-headline {\n  font-size: 1em;\n  font-weight: bold;\n  margin: 0;\n  margin-bottom: 2px;\n  height: 1.2em;\n  word-break: break-all;\n  overflow: hidden;\n  line-height: 1em;\n}\n\n.rs-sub-headline {\n  word-break: break-all;\n  overflow: hidden;\n  color: #666;\n  font-size: 0.92em;\n  height: 1.2em;\n}\n.rs-big-headline {\n  font-size: 1.625em;\n  font-weight: normal;\n  text-align: center;\n  margin-top: 20px;\n  margin-bottom: 20px;\n}\n\n/* BUTTONS  */\n.rs-button {\n  font: inherit;\n  color: inherit;\n  background-color: transparent;\n  border: 1px solid #dcdcdc;\n  border-radius: 3px;\n  cursor: pointer;\n}\n.rs-button-small {\n  padding: 0.5em 0.6em;\n  margin-left: 0.3em;\n  transition: border-color 300ms ease-out;\n}\n.rs-button-wrap {\n  margin-top: 10px;\n}\n\n.rs-button-wrap img,\n.rs-button-wrap svg {\n  float: left;\n  margin-right: 0.6em;\n  width: 40px;\n  height: 40px;\n}\n\n.rs-button-big {\n  padding: 15px 10px;\n  margin-bottom: 10px;\n  display: block;\n  width: 100%;\n  text-align: left;\n  transition: box-shadow 200ms;\n}\n.rs-button-big > div {\n  font-size: 1.125em;\n  padding: 10px 0;\n}\n.rs-button-big:hover {\n  box-shadow: 0 1px 2px 0 rgba(0,0,0,0.1), 0 3px 8px 0 rgba(0,0,0,0.2);\n}\n.rs-button-big:active {\n  background-color: #eee;\n  box-shadow: 0 1px 2px 0 rgba(0,0,0,0.1), 0 3px 8px 0 rgba(0,0,0,0.2);\n}\n.rs-button-big:last-child {\n  margin-bottom: 0;\n}\n\n.rs-content {\n  padding: 0 10px 10px 10px;\n}\n\n\n.rs-state-choose .rs-main-logo,\n.rs-state-sign-in .rs-main-logo {\n  margin-left: 45%;\n  float: none;\n}\n\n.rs-sign-in-form input[type=text] {\n  padding: 15px 10px;\n  display: block;\n  width: 100%;\n  font: inherit;\n  height: 52px;\n  border: 1px solid #aaa;\n  border-radius: 0;\n  box-shadow: none;\n}\n.rs-sign-in-form input[type=submit] {\n  padding: 15px 10px;\n  margin-top: 20px;\n  margin-bottom: 15px;\n  display: block;\n  width: 100%;\n  border: none;\n  border-radius: 3px;\n  background-color: #3fb34f;\n  font: inherit;\n  color: #fff;\n  transition: box-shadow 200ms, background-color 200ms;\n}\n.rs-sign-in-form input[type=submit]:hover {\n  cursor: pointer;\n  background-color: #4BCB5D;\n  box-shadow: 0 1px 2px 0 rgba(0,0,0,0.1), 0 3px 8px 0 rgba(0,0,0,0.2);\n}\n.rs-sign-in-form input[type=submit]:active {\n  background-color: #3fb34f;\n}\n.rs-sign-in-form input[type=submit]:disabled,\n.rs-sign-in-form input[type=submit]:disabled:hover {\n  background-color: #aaa;\n}\n\n.rs-sign-in-error.rs-hidden,\n.rs-box-error.rs-hidden {\n  height: 0;\n}\n\n.rs-sign-in-error.rs-visible,\n.rs-box-error.rs-visible {\n  height: auto;\n  border-radius: 3px;\n  padding: 0.5em 0.5em;\n  margin-top: 0.5em;\n  text-align: center;\n  background-color: rgba(255,0,0,0.1);\n  color: darkred;\n}\n\n.rs-box-error {\n  display: flex;\n  flex-direction: row;\n}\n\n.rs-box-error .rs-error-message {\n  flex: auto;\n}\n\n /*Choose provider box */\n.rs-box-choose {\n  text-align: center;\n  overflow: hidden;\n}\n\n.rs-box-choose p {\n  margin-top: 0;\n  margin-bottom: 20px;\n  line-height: 1.4em;\n}\n\n/*Connected box */\n.rs-box-connected {\n  display: flex;\n  flex-direction: row;\n  height: 40px;\n  transition: height 0s;\n}\n.rs-connected-text {\n  flex: auto;\n  min-width: 0;\n}\n.rs-box-connected .rs-user {\n  font-weight: bold;\n  text-overflow: ellipsis;\n  overflow: hidden;\n  word-break: keep-all;\n}\n.rs-connected-buttons, .rs-error-buttons {\n  flex: none;\n}\n.rs-disconnect:hover {\n  border-color: #FF2D2D;\n}\n.rs-disconnect:hover .rs-icon{\n  fill: #FF2D2D;\n}\n.rs-sync:hover {\n  border-color: #FFBB0C;\n}\n.rs-sync:hover .rs-icon {\n  fill: #FFBB0C;\n}\n.rs-sync.rs-rotate {\n  border-color: #FFBB0C;\n}\n.rs-sync.rs-rotate .rs-icon {\n  fill: #FFBB0C;\n  animation: rs-spin 1s linear infinite;\n}\n@keyframes rs-spin { 100% { transform: rotate(360deg); transform:rotate(360deg); } }\n\n/* Floating widget styles (top right corner) */\n.rs-floating {\n  position: fixed;\n  top: 0;\n  right: 0;\n}\n\n\n/* Small/mobile screens */\n@media screen and (max-width: 420px) {\n  .rs-widget {\n    font-size: 100%;\n    transition: all 300ms ease-out;\n    max-width: 400px;\n  }\n  .rs-floating {\n    position: relative;\n    top: auto;\n    right: auto\n  }\n  .rs-closed {\n    max-width: 56px;\n  }\n  .rs-state-choose,\n  .rs-state-sign-in {\n    position: fixed;\n    top: 0;\n    left: 0;\n    bottom: 0;\n    right: 0;\n    max-width: 100%;\n  }\n}\n\n/* remove dotted outline border on Firefox */\n.rs-widget a:focus,\n.rs-widget a:active,\n.rs-widget button:focus,\n.rs-widget input:focus {\n  outline:none;\n}\n.rs-widget button::-moz-focus-inner, \n.rs-widget input[type=\"button\"]::-moz-focus-inner,\n.rs-widget input[type=\"submit\"]::-moz-focus-inner {\n  border:0;\n}\n\n/* prevent rounded buttons on mobile Safari */\n.rs-widget input[type=\"button\"],\n.rs-widget input[type=\"submit\"] {\n  -webkit-appearance: none;\n}\n"

